import React, { useState } from 'react';
//antd imports
import { Form, Input, Button, Spin, message } from 'antd';
import { EditOutlined, CloseOutlined } from '@ant-design/icons';
//axios
import axios from 'axios';
//
const layout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 24 },
};

//-----------------------------------------------------------------------
const EditDp = (props) => {
  const [spin, setSpin] = useState(false);
  const [dataChanged, setDataChanged] = useState(false);
  const [form] = Form.useForm();
  const token = localStorage.getItem('token');
  //
  const onFinish = (values) => {
    console.log(values);
    setSpin(true);
    setDataChanged(true);
    const recievedValue = values.profileImage;
    const source = recievedValue.substring(0, 24);
    let link;
    if (source == 'https://drive.google.com') {
      const id = recievedValue.split('d/').pop().split('/view')[0];
      link = 'https://drive.google.com/thumbnail?id=' + id;
    } else {
      link = recievedValue;
    }

    const updatedData = {
      profilePicture: link,
    };
    console.log('data going to upload', updatedData);
    axios
      .put(
        `https://ginnovation-server.herokuapp.com/api/profile/data`,
        updatedData,
        {
          headers: { Authorization: `Bearer ${token}` },
        }
      )
      .then((resp) => {
        setSpin(false);
        console.log(resp);
        if (resp.data.status === 'success') {
          message.success('Successfully Edited Data');
          props.handleClose(true);
        } else {
          message.warning('Oops! Something went wrong');
        }
      })
      .catch((err) => {
        setSpin(false);
        console.log(err);
        message.error('Ouch! An error ocured');
      });
  };
  //
  return (
    <div className='modal-container'>
      {spin && (
        <div className='modal-container'>
          <Spin size='large' />
        </div>
      )}
      <div className='edit-modal'>
        <h4>
          Edit Profile Picture <EditOutlined className='icon' />
          <CloseOutlined
            className='close-btn'
            onClick={() => {
              props.handleClose(dataChanged);
            }}
          />
        </h4>

        <div className='form-container'>
          <Form
            form={form}
            name='control-hooks'
            onFinish={onFinish}
            initialValues={{
              profileImage: props.data.profilePicture,
            }}
          >
            <Form.Item name='profileImage' className='mb-5'>
              <Input placeholder='Image Link' />
            </Form.Item>

            <Form.Item className='btn-container mt-3'>
              <Button
                type='secondary'
                className='mr-2'
                onClick={props.handleClose}
              >
                Cancel
              </Button>
              <Button type='primary' htmlType='submit'>
                Save Changes
              </Button>
            </Form.Item>
          </Form>
        </div>
      </div>
    </div>
  );
};
export default EditDp;
